import { Component, OnInit } from '@angular/core';
import { MenuService } from '../../../../../auth/_services/menu.service';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { Router, ActivatedRoute } from "@angular/router";

@Component({
    selector: 'app-menu-list',
    templateUrl: "menu-list.component.html",
    styles: []
})
export class MenuListComponent implements OnInit {
    form: FormGroup;
    private formSumitAttempt: boolean;

    constructor(private toastr: ToastrService, private router: Router, private _menuService: MenuService, private formBuilder: FormBuilder) { }
    public menuData = '';
    public searchValue = "";
    public dropDownValue = '';
    public noOfPage = '';
    public selectedStatus = '';
    p: number = 1;

    ngOnInit() {

        /*user permission*/
        let currentUser = JSON.parse(localStorage.getItem('currentUser'));
        let permission = currentUser.success.permission;
        if (permission.product_menu != 1) {
            this.toastr.error('You have not permission to visit this page.', 'Error Message');
            this.router.navigate(['/']);
        }
        /*user permission*/

        localStorage.setItem('section', 'menu');
        this._menuService.getAll().subscribe(
            data => {
                this.menuData = data.success;
                //this.noOfPage = data.success.length;
            }
        );

        this.form = this.formBuilder.group({
            menu_name: [null, Validators.required],

            status: [null, Validators.required]
        });


    }


    isFieldValid(field: string) {
        return (
            (!this.form.get(field).valid && this.form.get(field).touched) ||
            (this.form.get(field).untouched && this.formSumitAttempt)
        );
    }

    displayFieldCss(field: string) {
        return {
            'has-error': this.isFieldValid(field),
            'has-feedback': this.isFieldValid(field)
        };
    }

    onSubmit() {
        this.formSumitAttempt = true;
        if (this.form.valid) {
            let that = this;
            console.log(this.form.value.menu_name);
            this._menuService.getByName(this.form.value.menu_name)
                .subscribe(
                searchData => {
                    console.log(searchData);
                    if (searchData.success) {
                        this.toastr.error('Menu Already Exist', 'Error Message');
                    } else {
                        this._menuService.createMenu({ form_data: this.form.value }).subscribe(
                            data => {
                                //this.menuData = data.success;
                                console.log(data);
                                this.menuData = data.data;
                                this.toastr.success('Menu Added Successfully', 'Success Message');
                                this.reset();
                            }
                        );
                    }
                }
                );


        }
    }

    reset() {
        if (this.selectedStatus != '') {
            this.form.reset();
            this.selectedStatus = '';
            this.formSumitAttempt = false;
        }
    }




    public search() {
        this._menuService.searching({ search_value: this.searchValue, dropdown_value: this.dropDownValue }).subscribe(
            data => {
                this.menuData = data.success;
            }
        );
    }





    public menu_delete(del_id) {

        console.log(del_id);

        let value = confirm('Are you want to delete this menu?');

        if (value) {
            this._menuService.menuDelete(del_id).subscribe(
                data => {
                    this.menuData = data.data;
                }

            );

        }

    }


    public changeStatus(menu: any) {

        console.log('menu status', menu);

        if (menu.status == 'Active') {
            menu.status = 'Inactive';

            this._menuService.updateStatus(menu.id).subscribe(

                data => {
                    console.log('update', data);

                }
            );
        } else {
            menu.status = 'Active';
            this._menuService.updateStatus(menu.id).subscribe(
                data => {
                    console.log('update', data);
                }
            );
        }
    }





}
